<template>
  <div id="home-div">
    <el-container class="home-con">
      <el-header class="header-con">
        <el-row :gutter="0">
          <el-col :xs="0" :sm="10" :md="10" :lg="10" :xl="10"
            ><div class="home-title">凌云博客</div></el-col
          >
          <el-col :xs="0" :sm="1" :md="4" :lg="6" :xl="8"
            ><div style="height: 60px"></div
          ></el-col>
          <el-col :xs="24" :sm="13" :md="10" :lg="8" :xl="6">
            <el-menu
              :default-active="$store.getters.getActiveIndex"
              class="el-menu-demo"
              mode="horizontal"
              background-color="#FFFFFF"
              text-color="#343A40"
              active-text-color="#1C60F3"
              router
            >
              <el-menu-item index="/main">首页</el-menu-item>
              <el-menu-item index="/message">留言</el-menu-item>
              <el-menu-item index="/login" v-if="!$store.getters.getToken.token"
                >登录/注册</el-menu-item
              >
              <el-submenu index="/user" v-if="$store.getters.getToken.token">
                <template slot="title">我的账户</template>
                <el-menu-item index="/user/userInfo">个人资料</el-menu-item>
                <el-menu-item @click="logout">退出登录</el-menu-item>
              </el-submenu>
            </el-menu>
          </el-col>
        </el-row>
      </el-header>
      <el-main class="main-con">
        <div class="testdiv">
          <div>
            <el-row :gutter="0">
              <el-col :xs="24" :sm="24" :md="18" :lg="18" :xl="16">
                <router-view v-if="flag"></router-view>
              </el-col>
              <el-col :xs="24" :sm="24" :md="16" :lg="6" :xl="8">
                <div>
                  <RankList />
                </div>
                <div>
                  <TimeLine />
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div class="bottom_div">
          <div class="bottom_text">
            <div class="span_text_div">
              <span class="span_text">凌云博客</span>
              <el-divider direction="vertical"></el-divider>
              <span class="span_text">关于本站</span>
            </div>

            <div class="span_text_div">
              <span class="span_text">© 2023 - Ye Lingfa's blog</span>
            </div>
            <div class="span_text_div">
              <span class="span_text" @click="gotoBeiAn"
                >闽ICP备2022015900号</span
              >
            </div>
          </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
// import UserPage from "@/components/user/UserPage";
// import CategoryPage from "@/components/category/CategoryPage";
// import LinkPage from "@/components/link/LinkPage";
// import MusicPage from "@/components/music/MusicPage";
// import CarouselPage from "@/components/carousel/CarouselPage";
// import ArticlePage from "@/components/article/ArticlePage";
import RankList from "@/components/rankList/RankList";
import TimeLine from "@/components/timeLine/TimeLine";

export default {
  name: "MainPage",
  components: {
    // UserPage,
    // CategoryPage,
    // LinkPage,
    // MusicPage,
    // CarouselPage,
    // ArticlePage,
    RankList,
    TimeLine,
  },
  data() {
    return {
      flag: true,
    };
  },
  methods: {
    setActiveIndex() {
      this.$store.dispatch("asyncUpdatedActiveIndex","/main")
    },
    reload() {
      this.flag = false;
      this.$nextTick(() => {
        this.flag = true;
      });
    },
    gotoBeiAn() {
      window.location.href = "https://beian.miit.gov.cn/";
    },
    async logout() {
      const res = await this.$api.user.logout();
      if (res.data.code === 200) {
        this.$message.success("退出成功！");
        window.sessionStorage.removeItem("token");
        window.sessionStorage.removeItem("userInfo");
        this.$store.dispatch("asyncUpdatedToken", {
          email: "",
          headimg: "",
          phonenumber: null,
          token: "",
          username: "",
        });
        this.$store.dispatch("asyncUpdatedActiveIndex","/login")
        this.$router.push("/login");
      } else {
        window.sessionStorage.removeItem("token");
        this.$store.dispatch("asyncUpdatedToken", {
          email: "",
          headimg: "",
          phonenumber: null,
          token: "",
          username: "",
        });
        this.$message.error("退出失败！");
      }
    },

  },
  mounted() {
    // this.setActiveIndex();
  },
  provide() {
    return { reload: this.reload };
  },
};
</script>

