  box-shadow: 0px 15px 8px -18px #5e5e5e;
  position: fixed;
  width: 100%;
  right: 0;
  top: 0;
  z-index: 999;
  padding: 0;
}
.el-menu {
  border: 0;
}
/* .header-con {
  width: 100%;

  display: flex;
  justify-content: space-between;
  margin: auto;
  height: 70px;
} */
.home-title {
  font-size: 18px;
  margin-top: 1em;
  /* margin-left: 0; */
}
.el-header,
.el-footer {
  background-color: #ffffff;
}

/* .el-aside {
  background-color: #d3dce6;
  height: calc(100vh - 70px);
} */

.el-main,
.main-con {
  width: 100%;
  margin: auto;
  padding: 0;

  /* height: calc(100vh - 60px); */
}

.home-title {
  margin-left: 13%;
}
.bottom_div {
  background-color: #21252b;
  height: 98px;
  position: relative;
}
.bottom_text {
  width: 350px;
  height: 21px;
  margin: auto;
  color: #ffffff;
}
.span_text {
  text-align: center;
  margin-top: 4px;
}
.span_text_div {
  text-align: center;
  padding-top: 8px;
}
.span_text:hover {
  color: #c353e5;
  cursor: pointer;
  font-size: 15px;
}
</style>

