<template>
  <div>
    <div class="ranks_div" v-if="!isphone">
      <el-card class="box-card" v-loading="!$store.getters.getRankList">
        <div slot="header" class="clearfix">
          <span>热门文章排行</span>
        </div>
        <div class="item_div">
          <div>
            <div
              @click="gotoArticleDetail(rank.id)"
              v-for="rank in $store.getters.getRankList"
              :key="rank.id"
              class="text item"
            >
              <img
                style="width: 20px; height: 20px; margin-top: -10px"
                src="../../assets/img/火.png"
                alt=""
              />
              {{ rank.articletitle }}
              <i style="margin-left: 20px" class="el-icon-view">
                <span> {{ bigNumberTransform(rank.review) }} </span></i
              >
            </div>
          </div>
        </div>
      </el-card>
    </div>
<!-- 手机适配 -->
    <div class="ranks_div_phone" v-if="isphone">
      <el-card class="box-card box-card_phone" v-loading="!$store.getters.getRankList.length">
        <div slot="header" class="clearfix_phone">
          <span>热门文章排行</span>
        </div>
        <div class="item_div_phone">
          <div>
            <div
              @click="gotoArticleDetail(rank.id)"
              v-for="rank in $store.getters.getRankList"
              :key="rank.id"
              class="text_phone item"
            >
              <img
                style="width: 20px; height: 20px; margin-top: -10px"
                src="../../assets/img/火.png"
                alt=""
              />
              {{ rank.articletitle }}
              <i style="margin-left: 20px" class="el-icon-view">
                <span> {{ bigNumberTransform(rank.review) }} </span></i
              >
            </div>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "RankList",
  inject: ["reload"],
  data() {
    return {
      isphone:false,
      // loading:true,
      rankList: [],
    };
  },
  methods: {
    async getHotArticle() {
      const res = await this.$api.article.getHotArticle();
      if (res.data.code === 200) {
        // this.rankList = res.data.data;
        this.$store.dispatch("asyncUpdatedRankList", res.data.data);
      } else {
        this.$message.error("获取热门文章失败！请稍后重试");
      }
    },

    async gotoArticleDetail(id) {
      // const res = await this.$api.article.getArticleDetailById(id);
      // if (res.data.code === 200) {
      //   window.sessionStorage.setItem(
      //     "articleDetail",
      //     JSON.stringify(res.data.data)
      //   );
      // this.$parent.setkey()
      if (window.location.pathname === "/articleDetail/" + id) {
        this.$message.info("你已经在看啦！");
        return;
      }

      this.$router.push("/articleDetail/" + id);
      // window.location.reload()
      // this.reload();
      // } else {
      //   this.$message.error("获取文章详情失败！请稍后再试");
      //   this.$router.push("/error");
      // }

      this.reload();
    },
    computedWidth() {
      var width = window.document.body.clientWidth;
      if (width < 500) {
        this.isphone = true
      } else {
        this.isphone = false
      }
    },
  },
  mounted() {
    this.getHotArticle();
    this.computedWidth()
  },
  computed: {
    bigNumberTransform() {
      return function (value) {
        const newValue = ["", "", ""];
        let fr = 1000;
        let num = 3;
        let text1 = "";
        let fm = 1;
        while (value / fr >= 1) {
          fr *= 10;
          num += 1;
          // console.log('数字', value / fr, 'num:', num)
        }
        if (num <= 4) {
          // 千
          newValue[0] = parseInt(value / 1000) + "";
          newValue[1] = "千";
        } else if (num <= 8) {
          // 万
          text1 = parseInt(num - 4) / 3 > 1 ? "千万" : "万";
          // tslint:disable-next-line:no-shadowed-variable
          fm = text1 === "万" ? 10000 : 10000000;
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        } else if (num <= 16) {
          // 亿
          text1 = (num - 8) / 3 > 1 ? "千亿" : "亿";
          text1 = (num - 8) / 4 > 1 ? "万亿" : text1;
          text1 = (num - 8) / 7 > 1 ? "千万亿" : text1;
          // tslint:disable-next-line:no-shadowed-variable
          fm = 1;
          if (text1 === "亿") {
            fm = 100000000;
          } else if (text1 === "千亿") {
            fm = 100000000000;
          } else if (text1 === "万亿") {
            fm = 1000000000000;
          } else if (text1 === "千万亿") {
            fm = 1000000000000000;
          }
          if (value % fm === 0) {
            newValue[0] = parseInt(value / fm) + "";
          } else {
            newValue[0] = parseFloat(value / fm).toFixed(2) + "";
          }
          newValue[1] = text1;
        }
        if (value < 1000) {
          newValue[0] = value + "";
          newValue[1] = "";
        }
        return newValue.join("");
      };
    },
  },
};
</script>

<style>
.ranks_div {
  margin-left: 15px;
  margin-top: 70px;
}
.ranks_div_phone {
  margin-top: 18px;
}
.clearfix_phone{
  font-size: 14px;
  
}
.box-card_phone{
  padding: 0 !important;
}
.el-card__body {
  padding: 0;
}
.text {
  font-size: 14px;
}
.text_phone {
  font-size: 12px;
}

.item {
  margin-bottom: 18px;
}

.box-card {
  width: 100%;
  height: auto;
  border-radius: 10px;
}
.item_div {
  margin-top: 10px;
  margin-left: 20px;
}
.item_div_phone {
  margin-top: 10px;
  margin-left: 20px;
}
</style>