<template>
  <div class="timeline-div">
    <div class="block">
      <el-timeline class="timeline">
        <el-timeline-item
          v-for="article in articleList"
          :key="article.id"
          color="#C353E5"
          class="timeline_item"
          :timestamp="timePase(article.createtime)"
          placement="top"
        >
          <el-card class="timeline_card">
            <div @click="gotoArticleDetail(article.id)">
              <h4>{{ article.articletitle }}</h4>
              <p>
                {{ article.createbyName + " 提交于 " + article.createtime }}
              </p>
            </div>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </div>
  </div>
</template>

<script>
export default {
  name: "TimeLine",
  inject: ["reload"],
  data() {
    return {
      articleList: [],
    };
  },
  methods: {
    async initArticleList() {
      const res = await this.$api.article.getTimeArticle();
      if (res.data.code === 200) {
        this.articleList = res.data.data;
      } else {
        this.$message.error("获取时间线失败！请稍后重试");
      }
    },

    async gotoArticleDetail(id) {
      // const res = await this.$api.article.getArticleDetailById(id);
      // if (res.data.code === 200) {
      //   window.sessionStorage.setItem(
      //     "articleDetail",
      //     JSON.stringify(res.data.data)
      //   );
      if (window.location.pathname === "/articleDetail/" + id) {
        this.$message.info("你已经在看啦！");
        return;
      }
      // this.$router.push("/main");
      this.$router.push("/articleDetail/" + id);
      this.reload();

      // } else {
      //   this.$message.error("获取文章详情失败！请稍后再试");
      //   this.$router.push("/error");
      // }
    },
  },
  mounted() {
    this.initArticleList();
  },
  computed: {
    timePase() {
      return function (time) {
        var date = new Date(time); // 初始化日期
        var year = date.getFullYear();
        var month = date.getMonth() + 1; // 获取月份
        var day = date.getDate(); // 获取具体日
        return year + "/" + month + "/" + day;
      };
    },
  },
};
</script>

<style>
.el-card__body {
  padding: 0;
}
.timeline-div {
  margin-top: 15px;
  width: 100%;
}
.timeline {
  padding-left: 28px;
}
.timeline_card {
  padding-left: 20px;
  border-radius: 10px;
}
.el-card__header {
  background-color: #f7e9fa;
}
</style>